import React,{useState} from "react";
import LoaderContext from "./loader-context";

const LoaderContextProvider = (props) =>{

    const [isActive,setIsActive] = useState(false);
    const [isUpdate,setIsUpdate] = useState(false);
    const [isDelete,setIsDelete] = useState(false);
    const [item,setItem] = useState({});

    const isActiveCountry = (isActive) =>{
        setIsActive(isActive);
    }

    const isUpdateFun = (isUpdate) =>{
        setIsUpdate(isUpdate);
    }

    const isDeleteFun = (isDelete) =>{
        setIsDelete(state => ({ ...state, isDelete: isDelete }));
        // setIsDelete(isDelete);
    }

    const isSetItem = (item) =>{
        setItem(item);
    }


    const loadingContext = {
        setIsActive:isActiveCountry,
        isActive:isActive,
        setItem:isSetItem,
        item:item,
        setIsUpdate:isUpdateFun,
        isUpdate:isUpdate,
        setIsDelete:isDeleteFun,
        isDelete:isDelete
    };

   
   

    return <LoaderContext.Provider value={loadingContext}>
        {props.children}
    </LoaderContext.Provider>

}

export default LoaderContextProvider;

