import React, { Fragment } from 'react'
import ReactDOM from 'react-dom';
import classes from './SpinnerModal.module.css';
import LoadingSpinner from '../Spinner/LoadingSpinner';

const SpinnerModal = (props) => {

    const BackDrop = () =>{
        return<div className={classes.backdrop} ></div>;
    }

    const Overlay = (props) =>{
        return <div className={classes.modal}>{<LoadingSpinner></LoadingSpinner>}</div>;
    }

    const element = document.getElementById('overlays');


  return (
    <Fragment>
        {ReactDOM.createPortal(<BackDrop></BackDrop>,element)}
        {ReactDOM.createPortal(<Overlay></Overlay>,element)}
    </Fragment>
  )
}

export default SpinnerModal