import React,{useState} from "react";
import "../../App.css";
import SearchData from "../Search/SearchData";
import TableView from "../TableData/TableView";

const ViewDetails = (props) => {

    const [q,setQ] = useState('');

    const setQueryParam = (queryParam) =>{
        setQ(queryParam);
    }

  return (
    <div id="wrapper-box">
      <h3 id="title-heade">View Details</h3>
      {<SearchData setQueryParam = {setQueryParam}></SearchData>}
      {<TableView query={q} stateId={props.stateId} setLoader={props.setLoader}></TableView>}
    </div>
  );
};

export default ViewDetails;
