import React,{useContext,useState,useEffect} from 'react';
import '../../App.css';
import Button from '../UI/Button/Button';
import LoaderContext from '../../context/loader-context';

const SaveReset = (props) =>{

  const [btnName,setBtnName] = useState('Save');
  const ctx = useContext(LoaderContext);
  const [isChecked,setIsChecked] = useState(false);
 

  const changeHandler = (event) =>{
     console.log(event.target.checked)
     ctx.setIsActive(event.target.checked);
     setIsChecked(event.target.checked);
  }

  const onClickResetHandler = (event) =>{
    event.preventDefault();
    ctx.setIsActive(false);
    props.onReset();
    setIsChecked(false);
    setBtnName('Submit');
    ctx.setIsUpdate(false);
  }

  const onClickSaveHandler = (event) =>{
    event.preventDefault();
    props.onSave();
    // setIsChecked(false);
    // setBtnName('Submit');
    // ctx.setIsUpdate(false);
  }

  useEffect(()=>{
    console.log("save used effect item"+ctx.item)
    if(Object.keys(ctx.item).length !== 0){
      setBtnName('Update');
      ctx.setIsUpdate(true);
      ctx.setIsActive(ctx.item.IsActive);
      setIsChecked(ctx.item.IsActive);
    }
   
 },[ctx.item]);

 useEffect(()=>{
  console.log("reset Value called"+ctx.isUpdate)
  if(ctx.isUpdate){
    setBtnName('Update');
    setIsChecked(ctx.item.IsActive);
  }else{
    setBtnName('Submit');
    setIsChecked(false);
  }
 
},[ctx.isUpdate]);

    return <React.Fragment>
          <div className='box'>
            <div className="leftCheckBox">
              <input type='checkbox' checked={isChecked} onChange={changeHandler} className='marginTop'></input>
              <div className='marginTopActive'>Active</div>
            </div>
            <div className="rightSaveButton">
              <Button onClick={onClickResetHandler}>Reset</Button>
              <Button onClick={onClickSaveHandler}>{btnName}</Button>
              
            </div>
          </div>
    </React.Fragment>
}

export default SaveReset;