import React from 'react'

const LoaderContext = React.createContext(
    {setIsActive:(isActive) =>{},
    isActive:false,
    item:{},
    setItem:(item)=>{},
    isUpdate:false,
    setIsUpdate:(isUpdate)=>{},
    isDelete:false,
    setIsDelete:(isDelete)=>{}}
);

export default LoaderContext;
