import React from "react";

const SearchData = (props) => {

  

    const demoArry = ['10','20','30'];
  
    const content  = demoArry.map((item)=>{
      return <option key={item}>{item}</option>
    });

    const onChangeHandler = (event) =>{
      props.setQueryParam(event.target.value);
    }


  return (
    <React.Fragment>
      <div className="box">
        <div className="leftCheckBox">
          <div className="itemsSelect"><p>Show</p></div>
          <select className="selectItem">
                {content}
          </select>
          <div className="itemsSelect"><p>entries</p></div>
        </div>
        <div className="rightSearchButton">
          <input className="select" onChange={onChangeHandler}></input>
          <div className="centerSearch">
            <p>Search:</p>
          </div>
        </div>
      </div>
    </React.Fragment>
  );
};

export default SearchData;
