import React, { useEffect, useState, useContext } from "react";
import "./TableView.css";
import { baseUrl } from "../../services/Constants";
import { header } from "../../services/Constants";
import LoaderContext from "../../context/loader-context";

export default function TableView(props) {
  const ctx = useContext(LoaderContext);
  const [state, setStateList] = useState([]);
  const [loadState, setLoadedList] = useState([]);
  let loadedState = [];

  // const data = [
  //     { name: "Anom", age: 19, gender: "Male" },
  //     { name: "Megha", age: 19, gender: "Female" },
  //     { name: "Subham", age: 25, gender: "Male"},
  //   ]
  // console.log(' Tableview  '+props.stateId);

  const callStatesByCountryIdApi = async () => {
    props.setLoader();
    loadedState = [];
    try {
      const { stateId } = props.stateId;
      let url = baseUrl + `api/CommonMaster/GetStateByCountryId/${stateId}`;
      console.log(url);
      const response = await fetch(url, header);

      if (!response.ok) {
        throw new Error("Request failed");
      }

      const data = await response.json();

      //   const loadedState = [];

      //   const sortedlist = data.sort((a, b) => {
      //     // console.log(a.Name);
      //     if (a.Name < b.Name) return -1
      //     return a.Name > b.Name ? 1 : 0
      //   })

      for (const pos in data) {
        loadedState.push(data[pos]);
      }
      setStateList(loadedState);
      setLoadedList(loadedState);
      console.log(loadedState.length);
    } catch (error) {
      console.log("error");
    }
    props.setLoader();
  };

  // const filterListFunCall = () => {
  //   const filteredList = [];
  //   if (state.length > 0) {
  //     for (const pos in state) {
  //       if (
  //         state[pos].Name.includes(props.query) ||
  //         state[pos].CountryName.includes(props.query) ||
  //         state[pos].Description.includes(props.query)
  //       ) {
  //         filteredList.push(state[pos]);
  //       }
  //     }
  //   }

  //   setStateList(filteredList);
  // };

  const filteredPersons = loadState.filter((person) => {
    return person.Name.toLowerCase().includes(props.query.toLowerCase());
  });

  useEffect(() => {
    console.log("callStatesByCountryIdApi outer called ");
    if (props.stateId !== "undefine") {
      console.log("callStatesByCountryIdApi called");
      callStatesByCountryIdApi();
    }
  }, [props.stateId]);

  useEffect(() => {
    console.log(props.query);
    setStateList(filteredPersons);
  }, [props.query]);

  const onEditHandler = (item) => {
    console.log("edit called "+item.Name);
    ctx.setIsDelete(false);
    ctx.setItem(item);
   
  };

  const onDeleteHandler = (item) => {
    console.log("delete called "+item.Name);
    ctx.setIsDelete(true);
    ctx.setItem(item);
    
  };

  let tableDataMapper = state.map((item, key) => {
    const isActiveCss = item.IsActive ? "circle_green" : "circle_red";
    const hoverVisibility = `${'deleteStyle'} ${'fa'}`;
    const action = (
      <div> 
        <div className={isActiveCss}></div>
      <div className="divIconRow">
        <i className={hoverVisibility} onClick={()=>onDeleteHandler(item)}>&#xf014;</i>
        <div className="edit-pencil" onClick={() => onEditHandler(item)}></div>
        
      </div></div>
     
    );
  

    return (
      <tr key={key}>
        <td id="tbd">{action}</td>
        <td id="tbd">{item.CountryName}</td>
        <td id="tbd">{item.Name}</td>
        <td id="tbd">{item.Description}</td>
        <td id="tbd">{item.DisplayOrder}</td>
      </tr>
    );
  });

  return (
    <div>
      <table id="table">
        <thead id="th">
          <tr >
            <td id="td">Active</td>
            <td id="td">Country</td>
            <td id="td">State</td>
            <td id="td">Description</td>
            <td id="td">Display Order</td>
          </tr>
        </thead>
        <tbody >
        {state.length>0 ? tableDataMapper:<tr><td colspan="5"><p style={{width:'100%',textAlign:'center',alignItems:'center'}}>No record available in this table.</p></td></tr>}
        </tbody>
      </table>
    </div>
  );
}
