import React,{ useState} from 'react';
import './App.css';
import SaveReset from './components/SaveData/SaveReset';
import ViewDetails from './components/ViewDetail/ViewDetails';
import FormData from './components/Form/FormData';
import SpinnerModal from './components/UI/Modal/SpinnerModal';
import LoaderContextProvider from './context/LoaderContextProvider';


const App = () => {

  const [stateId,setStateId] = useState();
  const [isSave,setIsSave] = useState('');
  const [isLoaderVisible,setIsLoaderVisible] = useState(false);
  

  const callStateDataApi = (stateId) =>{
    setStateId(state => ({ ...state, stateId: stateId }));
  }

  const setLoader = () =>{
    setIsLoaderVisible((prevState)=>!prevState)
  }

  const saveHandler = () =>{
    setIsSave(state => ({ ...state, isSave: 'save' }));
  }

  const resetHandler = () =>{
    setIsSave(state => ({ ...state, isSave: 'reset' }));
  }

  const content = <div id='container-bg'>
  <div id='wrapper-box'>
  <h3 id="title-heade">State Master</h3>
    {<FormData onBtnClick={isSave} setLoader = {setLoader} onStateChange={callStateDataApi}></FormData>}
    {<SaveReset onSave ={saveHandler} onReset={resetHandler} ></SaveReset>}
  </div>
  {<ViewDetails stateId = {stateId} setLoader = {setLoader}></ViewDetails>}
 
</div>;
  // console.log(ctx.isLoaderVisible);

  return (
      <LoaderContextProvider>
         {content}
        {isLoaderVisible && <SpinnerModal></SpinnerModal>}
      </LoaderContextProvider>
      

  );
};

export default App;
